.j-btn {
  outline: none;
  cursor: pointer;
  border-radius: 5px;
  user-select: unset;
  width: 120px;
  height: 35px;
  font-size: 16px;
  border: 1px solid #eee;
  background-color: transparent;
}
.j-btn--small {
  width: 80px;
  height: 25px;
  font-size: 12px;
}
.j-btn--large {
  width: 160px;
  height: 45px;
  font-size: 18px;
}
.j-btn-primary--default {
  color: #fff;
  background-color: #2979FF;
  border: 1px solid rgba(41, 121, 255, 0.9);
  box-shadow: 0px 4px 4px rgba(41, 121, 255, 0.25);
}
.j-btn-primary--default:hover {
  background-color: rgba(41, 121, 255, 0.8);
  border: 1px solid rgba(41, 121, 255, 0.6);
}
.j-btn-primary--light {
  color: #bbb;
  background-color: #ECF5FF;
  border: 1px solid rgba(236, 245, 255, 0.9);
  box-shadow: 0px 4px 4px rgba(236, 245, 255, 0.25);
}
.j-btn-primary--light:hover {
  background-color: rgba(236, 245, 255, 0.8);
  border: 1px solid rgba(236, 245, 255, 0.6);
}
.j-btn-primary--disabled {
